import {
  Diff,
  Fields,
  GameTable,
  PlayResult,
  TableListValues,
} from "pages/Tower";
import React, { Fragment } from "react";
import { repeat } from "utils";
import { RowComponent } from "../RowComponent";

import css from "./style.module.scss";

export const rowsAmount = 9;

const cellsAmount = {
  easy: 4,
  medium: 3,
  hard: 2,
  brutal: 4,
  wicked: 3,
};
const diff: Diff = "medium";
interface IProps {
  list: TableListValues[][];
  cellsAmount: 2 | 3 | 4;
  diff: Diff;
}

export const TableComponent = ({ list, cellsAmount, diff }: IProps) => {
  return (
    <div className={css.root}>
      {repeat(rowsAmount, (i) => {
        const index = rowsAmount - i;

        return (
          <Fragment key={`rowKey_#${i}`}>
            <RowComponent
              system={list[index - 1]}
              rowNumber={index - 1}
              cellsAmount={cellsAmount}
              diff={diff}
            />
          </Fragment>
        );
      })}
    </div>
  );
};
