import React, { FC, useLayoutEffect, useRef, useState } from "react";
import clsx from "clsx";

import { Button, useSoundSystemControl } from "components";

import {
  outlinesImages,
  TableListValues,
  towerImages,
} from "pages/Tower/controller";

import BadUrl from "./assets/bad.svg";
import GoodUrl from "./assets/good.svg";
import css from "./style.module.scss";

interface Props {
  system?: TableListValues;
  rowNumber: number;
  diff: keyof typeof towerImages;
}

export const ButtonComponent: FC<Props> = ({ system, diff }) => {
  const handleClick = () => {};

  const isBad = system?.status === "bad";
  const isSelected = system?.selected;

  const getImageLink = () => {
    const SelectedImage = towerImages[diff];
    if (isSelected) {
      const buttonStatus = system?.status;

      switch (buttonStatus) {
        case "bad":
          return <img src={BadUrl} alt="loss" />;

        case "good":
          return <img src={GoodUrl} alt="win" />;

        default:
          break;
      }
    }

    return <SelectedImage />;
  };

  return (
    <Button
      onClick={handleClick}
      className={clsx(
        css.pawButton,
        isBad && css.bad,
        isSelected && css.selected,
      )}>
      <span className={css.imgWrapper}>{getImageLink()}</span>
    </Button>
  );
};
