import React, { FC } from "react";
import { ITowerResult } from "../types";
import { GameComponent } from "./components/Game";

import css from "./style.module.scss";

export interface ITowersComponentProps extends ITowerResult {
  coin: string;
  profit: string;
  win: 1 | 0;
  payout: string;
}
export const TowersComponent: FC<ITowersComponentProps> = ({ ...result }) => {
  return (
    <div className={css.root}>
      <GameComponent {...result} />
    </div>
  );
};
