import React, { FC, Fragment, useEffect, useState } from "react";

import { GetCoinImg } from "components";
import { repeat } from "utils";

import { Diff, TableListValues } from "pages/Tower/controller";
import css from "./style.module.scss";
import { ButtonComponent } from "../ButtonComponent";

interface Props {
  cellsAmount: 2 | 3 | 4;
  system?: TableListValues[];
  rowNumber: number;
  diff: Diff;
}

export const RowComponent: FC<Props> = ({
  cellsAmount,
  rowNumber,
  system,
  diff,
}) => {
  return (
    <div className={css.root}>
      {repeat(cellsAmount, (i) => {
        const buttonSysValue = system?.find(({ value }) => value === i);
        return (
          <Fragment key={`rowKey_#${i}_Amount${cellsAmount}`}>
            <ButtonComponent
              system={buttonSysValue}
              rowNumber={rowNumber}
              diff={diff}
            />
          </Fragment>
        );
      })}
    </div>
  );
};
