import React from "react";
import clsx from "clsx";
import { GetCoinImg } from "components";
import { ReactComponent as StarsIcon } from "../../assets/stars.svg";
import { useGetLocale } from "providers";
import css from "./style.module.scss";

interface IProps {
  win: 0 | 1;
  multiplier: string;
  profit: string;
  coin: string;
}
export const ResultComponent = ({
  coin,
  multiplier,
  profit,
  win = 1,
}: IProps) => {
  const intl = useGetLocale();
  const isWin = win === 1;
  const title = isWin
    ? "gameTemplate.statistics.yourBetsTable.betData.resultNotify.winTitle"
    : "gameTemplate.statistics.yourBetsTable.betData.resultNotify.loseTitle";
  const text = isWin
    ? "gameTemplate.statistics.yourBetsTable.betData.resultNotify.winText"
    : "gameTemplate.statistics.yourBetsTable.betData.resultNotify.loseText";

  return (
    <div className={clsx(css.wrapper, win === 1 && css.win)}>
      <div className={css.root}>
        <span className={css.title}>{intl.formatMessage({ id: title })}</span>
        <span className={css.resultText}>
          {intl.formatMessage({ id: text })}
        </span>
        <div>
          <StarsIcon />
        </div>
        <div className={css.content}>
          <span className={css.multiplier}>
            {Number(multiplier).toFixed(2) + "x"}
          </span>
          <div className={css.profit}>
            <GetCoinImg className={css.coinImg} coin={coin} />
            {profit}
          </div>
        </div>
      </div>
    </div>
  );
};
