import React, { FC } from "react";

import { Diff, TableListValues } from "pages/Tower";
import { ITowerResult } from "../../../types";
import { ResultComponent } from "../ResultComponent";
import { TableComponent } from "../TableComponent";
import css from "./style.module.scss";

interface IProps extends ITowerResult {
  win: 1 | 0;
  coin: string;
  profit: string;
  payout: string;
}

const getDiff = (badLength: number, columns: number): Diff => {
  let diff: Diff = "easy";
  if (badLength === 1 && columns === 3) {
    diff = "medium";
  }
  if (badLength === 1 && columns === 2) {
    diff = "hard";
  }
  if (badLength === 3 && columns === 4) {
    diff = "brutal";
  }
  if (badLength === 2 && columns === 3) {
    diff = "wicked";
  }
  return diff;
};
const createList = (results: ITowerResult): TableListValues[][] => {
  const { bad_locs, g_size, sw_locs } = results;
  const list: TableListValues[][] = bad_locs.map((row, index) => {
    const cells: TableListValues[] = [];
    for (let i = 0; i < g_size; i++) {
      if (row.includes(i)) {
        cells[i] = {
          value: i,
          selected: sw_locs[index] === i,
          status: "bad",
        };
      } else {
        cells[i] = { value: i, selected: sw_locs[index] === i, status: "good" };
      }
    }
    return cells;
  });
  return list;
};
export const GameComponent: FC<IProps> = ({
  coin,
  win,
  profit,
  payout,
  ...props
}) => {
  return (
    <div className={css.root}>
      <TableComponent
        list={createList(props)}
        cellsAmount={props.g_size as 2 | 3 | 4}
        diff={getDiff(props.bad_locs[0].length, props.g_size)}
      />
      <ResultComponent
        coin={coin}
        multiplier={payout}
        profit={profit}
        win={win}
      />
    </div>
  );
};
